import React, { Component } from 'react';
import { connect } from 'react-redux';
import LayoutContentWrapper from '../components/utility/layoutWrapper';
import LayoutContent from '../components/utility/layoutContent';
import Button from '../components/uielements/button';
import notification from '../components/notification';
import AuthHelper from '../helpers/authHelper';

import SuperFetch from '../helpers/superFetch';

class AuthCheck extends Component {
  state = { loading: false, serverVariable: {} };
  checkDemo = () => {
    this.setState({ loading: true });
    AuthHelper.checkDemoPage(this.props.idToken).then(result => {
      if (result.error) {
        notification('error', result.error);
      } else {

        notification('success', `status: ${result.status}`, result.message);
      }
      this.setState({ loading: false });
    });
  };
  checkClientVariable = () => {
    Object.keys(process.env).map(obj =>notification('success', `status: ${obj}`, process.env[obj]))
  };
  checkServerVariable = () => {
    SuperFetch.post('checkServerVariable', {something:'server'}).then(result => {
      //console.log(result)
      if (result.error) {
        notification('error', result.error);
      } else {
        Object.keys(result.variable).map(obj =>notification('success', obj, process.env[obj]))
        this.setState({ serverVariable: result.variable });
      }
    })
  };
  render() {
    return (
      <LayoutContentWrapper style={{ height: '100vh' }}>
        <LayoutContent>
          <Button loading={this.state.loading} onClick={this.checkDemo}>
            Check Auth Status
          </Button>
          <Button onClick={this.checkClientVariable}>
            Check Client Variable
          </Button>
          <Button onClick={this.checkServerVariable}>
            Check Server Variable
          </Button>
          <Button onClick={this.checkGet}>
            Check GET
          </Button>
          {Object.keys(this.state.serverVariable).map(obj => <div key={obj}>{obj}  |  {this.state.serverVariable[obj]}</div>)}
        </LayoutContent>
      </LayoutContentWrapper>
    );
  }
}
export default connect(
  state => {
    return {
    idToken: state.Auth.idToken,
  }},
  {}
)(AuthCheck);
